@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@500;700&display=swap);
html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
body {
  line-height: 1;
}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}

/* * {
  outline: 1px solid green;
} */

h1 {
  font-size: 30px;
}

html,
body,
#root,
.App {
  height: 100%;
  font-size: 18px;
}

.App {
  display: grid;
  grid-template-rows: 80px auto 50px;
  font-family: Roboto;
  line-height: normal;
}

.login_block {
  display: flex;
  justify-content: center;
  align-items: center;
}

.login_block input {
  padding: 10px;
  font-size: 18px;
}

.submit {
  color: #fff;
  background-color: #1c1c1c;
}

header {
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: bold;
}

main {
  overflow: scroll;
}

.excerpt_container {
  display: flex;
  flex-direction: column;
  margin: 50px 20px;
}

.quote_symbol {
  width: 25px;
}

.excerpt_block {
  width: 93%;
  align-self: end;
}

#excerpt,
#details {
  margin-top: 19px;
}

.comment_block {
  background-color: #1c1c1c;
  color: white;
  position: relative;
  padding: 10px;
  margin-top: 29px;
}

.comment_block::before {
  content: "";
  width: 0;
  height: 0;
  border-top: 13px solid transparent;
  border-left: 13px solid #1c1c1c;
  position: absolute;
  top: -12px;
  left: 0;
}

nav {
  display: flex;
  background-color: #1c1c1c;
  color: white;
}

.nav-item {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 25%;
  border: 1px solid #2f2f2f;
}

.button_refresh {
  height: 40%;
}

